import React from "react";
import { useCookies } from "react-cookie";
import { FaBars } from "react-icons/fa";
import { HiUser } from "react-icons/hi";
import { IoMdAdd, IoMdLogOut } from "react-icons/io";
import { useSelector } from "react-redux";
import { useAuth } from "../Context/Auth";
import { NavLink, useNavigate, useParams } from "react-router-dom";
import styled from "styled-components";

const NavigationBarV2 = () => {
  const auth = useAuth();
  const param = useParams();
  const navigate = useNavigate();
  const { title } = useSelector((state) => state.navBarTitle);
  // eslint-disable-next-line no-unused-vars
  const [cookie, setCookie, removeCookie] = useCookies(["log"]);

  // document.onclick = (e) => {
  //   const opPanel = document.getElementById("option-panel");
  //   if (e.target.id === "brand" || e.target.id === "brandImg") {
  //     if (opPanel.style.display === "none") {
  //       opPanel.style.display = "block";
  //     } else {
  //       opPanel.style.display = "none";
  //     }
  //   } else {
  //     if (opPanel !== null) {
  //       opPanel.style.display = "none";
  //     }
  //   }

  //   const sideNav = document.getElementById("side-nav");
  //   if (e.target.id === "bar" || e.target.id === "barIcon") {
  //     sideNav.style.display = "block";
  //   } else {
  //     if (sideNav !== null) {
  //       sideNav.style.display = "none";
  //     }
  //   }
  // };

  React.useEffect(() => {
    console.log("top-navigation bar");
  }, []);

  return (
    <header>
      <TopNavigation>
        <BarIcon id="bar">
          <FaBars id="barIcon" />
        </BarIcon>
        <Title>{title}</Title>
        <Tools>
          <Link to={`/t/${param.tid}/class/create`}>
            <IoMdAdd /> Create
          </Link>
          <Brand id="brand">
            <Image
              id="brandImg"
              src={auth.user.image || require("../components/images/man.png")}
            />
          </Brand>
          <OptionPanel id="option-panel">
            <ButtonDiv onClick={() => {}}>
              <HiUser /> Profile
            </ButtonDiv>
            <ButtonDiv
              onClick={() => {
                removeCookie("log", { path: "/" });
                auth.logout();
                navigate("/", { replace: true });
              }}
            >
              <IoMdLogOut /> Logout
            </ButtonDiv>
          </OptionPanel>
        </Tools>
      </TopNavigation>
    </header>
  );
};

export default NavigationBarV2;

const TopNavigation = styled.nav`
  height: 70px;
  background: rgba(19, 15, 64, 1);
  display: flex;
  align-items: center;
  padding-left: 1rem;
  padding-right: 1.5rem;
  // box-shadow: 1px 1px 5px 5px rgba(3, 3, 3, 0.5);
`;

const BarIcon = styled.div`
  display: none;

  @media (max-width: 820px) {
    display: flex;
    width: 40px;
    height: 40px;
    border: 2px solid #fff;
    border-radius: 3px;
    color: #fff;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    cursor: pointer;
    background: rgba(19, 15, 64, 1);
    position: relative;
  }
`;

const Brand = styled.div`
  width: 40px;
  height: 40px;
  border-radius: 50px;
  background: rgba(241, 242, 246, 1);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  overflow: hidden;
  cursor: pointer;
  position: relative;
`;

const Image = styled.img`
  width: 100%;
  height: auto;
`;

const Title = styled.h1`
  font-size: 1.1rem;
  font-weight: 400;
  color: #fff;
  margin-bottom: 0;
  padding-left: 10px;
`;

const Tools = styled.div`
  margin: 0 1.5rem 0 auto;
  position: relative;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  @media (max-width: 820px) {
    display: none;
  }
`;

const OptionPanel = styled.div`
  position: absolute;
  display: none;
  width: 120px;
  right: -1rem;
  top: 45px;
  background: rgba(223, 249, 251, 1);
  border: 1px solid rgba(3, 3, 3, 0.3);
  border-radius: 5px;
  padding: 0.5rem 0rem;
  box-shadow: 1px 1px 5px 5px rgba(3, 3, 3, 0.2);
  z-index: 1;

  &::before {
    content: "";
    border-style: solid;
    border-width: 10px 10px 10px 10px;
    border-color: transparent transparent rgba(223, 249, 251, 1) transparent;
    position: absolute;
    top: -15px;
    right: 25px;
  }
`;

const ButtonDiv = styled.div`
  height: 30px;
  font-weight: 500;
  cursor: pointer;
  padding: 0.2rem 0.5rem;
  padding-left: 0.8rem;

  &:hover {
    background: rgba(72, 52, 212, 1);
    color: #fff;
  }
`;

const Link = styled(NavLink)`
  margin-right: 5px;
  text-decoration: none;
  color: #fff;
  font-weight: 500;
  display: flex;
  align-items: center;
`;
